.Exchange-params {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.Exchange-inputs,
.Exchange-labels {
  display: flex;
  flex-direction: column;
  height: 8em;
  width: 40%;
  justify-content: space-between;
}

.Exchange-labels {
  align-items: flex-start;
  padding-left: 5%;
}

.Exchange-inputs input[type="number"] {
  width: 4em;
  align-self: flex-end;
}

.Exchange-outputs {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: white;
  min-height: 500px;
  flex-direction: row;
  max-width: 800px;
  margin-top: 2em;
}

.Exchange-pile {
  display: flex;
  flex-direction: row;
  max-width: 22%;
  flex-wrap: wrap;
  align-items: flex-start;
}

.Exchange-pile > p {
  width: 100%;
  min-height: 3em;
}
